@import  "../../style/_vars.scss";

.button {
    width: 20%;
    background-color: $yellowBg; 
    font-size: $buttonFs; 
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    cursor: pointer;
    transition: background-color 0.3s;
  
    &:hover {
      background-color: darken($yellowBg, 10%); 
    }
  
    &:active {
      transform: scale(0.98);
    }
  }